@import url('https://fonts.googleapis.com/css?family=Muli&display=swap');

#faqBody {
    font-family: 'Muli', sans-serif;
    background-color: #f0f0f0;
    padding: 1px;
    height: 100vh;

    h1 {
        margin: 50px 0 30px;
        text-align: center;

    }
}

.faqContainer {
    max-width: 600px;
    margin: 0 auto;
}

.faq {
    background-color: transparent;
    border: 1px solid #9fa4a8;
    border-radius: 10px;
    margin: 20px 0;
    padding: 30px;
    position: relative;
    overflow: hidden;
    transition: 0.3s ease;
}

.faq.active {
    background-color: #fff;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.1), 0 3px 6px rgba(0, 0, 0, 0.1);

    .faqText {
        display: block;
    }

    .faqToggle {
        background-color: #9fa4a8;

        .fa-times {
            color: #fff;
            display: block;
        }

        .fa-chevron-down {
            display: none;
        }
    }

}

.faq.active::before,
.faq.active::after {
    content: '\f075';
    font-family: 'Font Awesome 5 Free';
    color: #2ecc71;
    font-size: 7rem;
    position: absolute;
    opacity: 0.2;
    top: 20px;
    left: 20px;
    z-index: 0;
}

.faq.active::before {
    color: #3498db;
    top: -10px;
    left: -30px;
    transform: rotateY(180deg);
}


.faqTitle {
    margin: 0 35px 0 0;
}

.faqText {
    display: none;
    margin: 30px 0 0;
}

.faqToggle {
    background-color: transparent;
    border: 0;
    border-radius: 50%;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 16px;
    padding: 0;
    position: absolute;
    top: 30px;
    right: 30px;
    height: 30px;
    width: 30px;

    .fa-times {
        display: none;
    }
}


.faqToggle:focus {
    outline: 0;
}